.page-main {
  min-width: 320px;
  margin: 0 auto;
  // background: url(../img/bg-back-mobile@1x.jpg) no-repeat 0px -60px,  #283645;

  &__timer {
    display: none;
  }
}

@media (min-width: 660px) {
  .page-main {
    // width: 660px;
    min-width: 660px;
    margin: 0 auto;
    // background-color: red;

    &__timer {
      position: relative;
      width: 660px;
      margin: 0 auto;
      margin-top: 59px;
      // margin-bottom: 106px;
      display: flex;
      justify-content: start;
      align-items: flex-start;
      // background: url("../img/timer-svg.png") no-repeat 18px 65px;
      // background-color: cornflowerblue;
    }

    &__timer svg {
      width: 238px;
      height: 276px;
      margin-top: 0;
      margin-left: 21px;
    }

    &__timer svg path {
      fill: #dedede;
    }

    &__twenty-four {
      display: block;
      position: absolute;
      z-index: 10;
      width: 202px;
      height: 202px;
      margin: 0;
      margin-top: 55px;
      margin-left: 39px;
      margin-right: 88px;
      box-sizing: border-box;
      border: 13px solid #d22856;
      border-radius: 50%;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 96px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.56;
      letter-spacing: normal;
      text-align: center;
      color: #d22856;
      padding-top: 8px;
    }

    &__slogan-wrapper {
      margin-top: 35px;
      margin-left: 70px;
      // margin-bottom: 137px;
      border-top: 1px solid rgba(255, 255, 255, 0);
      border-bottom: 1px solid rgba(255, 255, 255, 0);
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      justify-content: start;
      align-items: start;
      // background-color: lightblue;
    }

    &__timer-slogan {
      margin: 0;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 30px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.2;
      letter-spacing: normal;
      text-align: left;
      color: #283645;
      // background-color: seagreen;
    }

    &__timer-text {
      margin-top: 11px;
      width: 300px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.71;
      letter-spacing: normal;
      text-align: left;
      color: #283645;
    }

    &__break-one {
      display: none;
    }

    &__timer-link {
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.71;
      letter-spacing: normal;
      text-align: left;
      color: #d22856;
    }

    &__timer-link:hover {
      color: #283645;
    }

    &__timer-link:active {
      color: #283645;
      opacity: 0.3;
    }
  }
}

@media (min-width: 1200px) {
  .page-main {
    // width: 1200px;
    min-width: 1200px;
    margin: 0 auto;

    &__timer-container {
      background-color: #f2f2f2;
    }

    &__timer {
      width: 1200px;
      margin: 0 auto;
      display: flex;
      justify-content: start;
      align-items: flex-start;
      // background: url("../img/timer-svg.svg") no-repeat 130px 17px;
      background-color: #f2f2f2;
    }

    &__timer svg {
      width: 298px;
      height: 345px;
      margin-top: 11px;
      margin-left: 131px;
    }

    &__timer svg path {
      fill: #dedede;
    }

    &__twenty-four {
      width: 253px;
      height: 253px;
      margin-top: 80px;
      margin-left: 153px;
      margin-right: 90px;
      box-sizing: border-box;
      border: 17px solid #d22856;
      border-radius: 50%;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 120px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.56;
      letter-spacing: normal;
      text-align: center;
      color: #d22856;
      padding-top: 8px;
      // background-color: skyblue;
    }

    &__slogan-wrapper {
      margin-top: 98px;
      margin-left: 67px;
      // margin-right: 36px;
      margin-bottom: 112px;
      border-top: 1px solid rgba(255, 255, 255, 0);
      border-bottom: 1px solid rgba(255, 255, 255, 0);
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      justify-content: start;
      align-items: start;
      // background-color: #ffffff;
    }

    &__timer-slogan {
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 30px;
      font-weight: bold;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.3;
      letter-spacing: normal;
      text-align: left;
      color: #283645;
      // background-color: seagreen;
    }

    &__break-slogan {
      display: none;
    }

    &__break-three {
      display: none;
    }

    &__timer-text {
      width: 550px;
      margin-top: 21px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 18px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.67;
      letter-spacing: normal;
      text-align: left;
      color: #283645;
    }

    &__break-one {
      display: block;
    }

    &__break-two {
      display: none;
    }

    &__timer-link {
      margin-top: 13px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 18px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.67;
      letter-spacing: normal;
      text-align: left;
      color: #d22856;
    }
  }
}
