.connection {
  display: block;
  min-width: 320px;
  margin: 0 auto;
  // position: relative;
  background-color: #ffffff;
  border-top: 1px solid #ffffff;

  &__title {
    width: 230px;
    margin: 0 auto;
    margin-top: 40px;
    margin-bottom: 34px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 300;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.25;
    letter-spacing: normal;
    text-align: center;
    color: #283645;
  }

  &__block--one,
  &__block--two,
  &__block--three {
    // width: 260px;
    margin: 0 auto;
    // background-color: seagreen;
  }

  &__contacts {
    width: 210px;
    margin: 0 auto;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #283645;
    // background-color: teal;
  }

  &__phone {
    display: block;
    width: 224px;
    margin: 0 auto;
    margin-bottom: 18px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 300;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    text-decoration: none;
    color: #d22856;
    // background-color: turquoise;
  }

  &__phone:hover {
    color: #283645;
    text-decoration: underline;
  }

  &__phone:active {
    color: #283645;
    opacity: 0.3;
    text-decoration: none;
  }

  &__email {
    display: block;
    width: 240px;
    margin: 0 auto;
    margin-top: 0;
    // margin-left: 44px;
    margin-bottom: 20px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 300;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    text-decoration: none;
    color: #d22856;
    // background-color: turquoise;
  }

  &__email:hover {
    color: #283645;
    text-decoration: underline;
  }

  &__email:active {
    color: #283645;
    opacity: 0.3;
    text-decoration: none;
  }

  &__adress {
    display: block;
    width: 212px;
    margin: 0 auto;
    margin-top: 0;
    // margin-left: 57px;
    margin-bottom: 20px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 300;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #d22856;
    // background-color: turquoise;
  }

  &__iframe-container {
    // width: 320px;
    min-width: 320px;
    position: relative;
    margin: 0 auto;
    margin-top: 35px;
  }

  &__triangle {
    width: 100%;
    // min-width: 320px;
    margin: 0 auto;
    height: 34px;
    // border: 1px solid red;
    // background: url("../img/triangle-white-footer-svg.png") no-repeat center -88px;
    position: absolute;
    top: -1px;
    left: 0;
    right: 0;
  }

  &__map {
    display: block;
    // width: 320px;
    // min-width: 320px;
    width: 100%;
    height: 188px;
    margin: 0 auto;
    // margin-bottom: 50px;
    border: none;
    background: url("../img/bg-map-mobile@1x.png") no-repeat 0 300px, #ffffff;
  }
}

@media (min-width: 660px) {
  .connection {
    min-width: 660px;
    margin: 0 auto;
    // background-color: orchid;

    &__wrapper {
      // width: 542px;
      width: 600px;
      margin: 0 auto;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-wrap: wrap;
      // background-color: slateblue;
    }

    &__title {
      width: 370px;
      margin: 0 auto;
      margin-top: 45px;
      margin-bottom: 34px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 40px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.2;
      letter-spacing: normal;
      text-align: center;
      color: #283645;
      // background-color: violet;
    }

    &__block--one {
      // width: 250px;
      margin: 0;
      margin-top: 8px;
      margin-left: 28px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      // background-color: seagreen;
    }

    &__block--two {
      margin: 0;
      margin-top: 8px;
      margin-left: 88px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      // background-color: seagreen;
    }

    &__block--three {
      margin: 0 auto;
      margin-top: 24px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      // background-color: seagreen;
    }

    &__contacts {
      margin: 0;
    }

    &__email {
      margin-top: 4px;
    }

    &__adress {
      margin-top: 6px;
      line-height: 1.2;
    }

    &__iframe-container {
      min-width: 660px;
      margin-top: 13px;
      // background-color: royalblue;
    }

    &__triangle {
      min-width: 660px;
      height: 75px;
      // border: 1px solid red;
      // background: url("../img/triangle-white-footer-svg.png") no-repeat center -54px;
      position: absolute;
      top: -1px;
      left: 0;
      right: 0;
    }

    &__map {
      display: block;
      // min-width: 660px;
      width: 100%;
      // height: 366px;
      height: 371px;
      margin: 0 auto;
      // margin-bottom: 50px;
      border: none;
      // background: url("../img/bg-map-mobile@1x.png") no-repeat 0 300px, #ffffff;
    }
  }
}

