.main-photo {
  min-width: 320px;
  margin: 0 auto;
  background-color: #ffffff;
  // border-left: 1px solid #f2f2f2;
  // border-right: 1px solid #f2f2f2;
  box-sizing: border-box;

  &__header {
    min-width: 320px;
    // height: 171px;
    margin: 0 auto;
    background: url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center -68px, #283645;
    // background: url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center top, #283645;
    border-top: 1px solid rgba(255, 255, 255, 0);
    border-bottom: 1px solid rgba(255, 255, 255, 0);
    box-sizing: border-box;
    // padding-top: 68px;

    @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
      background: url("../img/picture/bg-back-mobile@2x.jpg") no-repeat center -68px, #283645;
    }
  }

  // &__header--open {
  //   min-width: 320px;
  //   // height: 171px;
  //   margin: 0 auto;
  //   background: url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center -68px, #283645;
  //   // background: url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center top, #283645;
  //   border-top: 1px solid rgba(255, 255, 255, 0);
  //   border-bottom: 1px solid rgba(255, 255, 255, 0);
  //   box-sizing: border-box;
  //   padding-top: 0;
  // }

  // &.open {
  //   min-width: 320px;
  //   // height: 171px;
  //   margin: 0 auto;
  //   background: url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center -68px, #283645;
  //   // background: url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center top, #283645;
  //   border-top: 1px solid rgba(255, 255, 255, 0);
  //   border-bottom: 1px solid rgba(255, 255, 255, 0);
  //   box-sizing: border-box;
  //   padding-top: 0;
  // }

  &__title {
    width: 168px;
    margin: 0 auto;
    margin-top: 29px;
    margin-bottom: 83px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 300;
    font-style: normal;
    line-height: 1.2;
    text-align: center;
    color: #ffffff;
  }

  &__recomendation {
    max-width: 252px;
    margin: 0 auto;
    margin-top: 46px;
    margin-bottom: 51px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    line-height: 2.14;
    text-align: center;
    color: #283645;
    // background-color: green;
  }

  &__block {
    position: relative;
    // max-width: 320px;
    margin: 0 auto;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
  }

  &__block--special {
    position: relative;
    width: 320px;
    margin: 0 auto;
    margin-bottom: 20px;
    // margin-bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
  }

  &__day-photo {
    position: absolute;
    top: 0;
    right: 20px;
    display: block;
    width: 140px;
    height: 36px;
    background-color: #d22856;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 12px;
    font-weight: bold;
    font-style: normal;
    line-height: 2;
    text-transform: uppercase;
    text-align: center;
    color: #ffffff;
    padding-top: 6px;
    box-sizing: border-box;
  }

  &__image--special {
    width: 320px;
    height: 137px;
  }

  &__link--special {
    position: relative;
    width: 320px;
    height: 137px;
  }

  &__image {
    width: 280px;
    height: 180px;
  }

  &__link {
    width: 280px;
    height: 180px;
  }

  &__comment-block {
    width: 280px;
    margin: 0 auto;
    // margin-bottom: 20px;
    padding-left: 17px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    border-top: 1px solid rgba(255, 255, 255, 0);
    background-color: #f2f2f2;
  }

  &__name-wrapper {
    margin-top: 17px;
    // margin-left: 17px;
    // background-color: violet;
  }

  &__name {
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: bold;
    font-style: normal;
    line-height: 1.71;
    color: #283645;
  }

  &__time {
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 12px;
    font-weight: 300;
    font-style: normal;
    line-height: 1.71;
    color: #283645;
    text-transform: uppercase;
  }

  &__text {
    max-width: 260px;
    // margin-left: 17px;
    margin-top: 2px;
    margin-bottom: 0;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.75;
    letter-spacing: normal;
    color: #283645;
    // background-color: green;
  }

  &__break {
    display: none;
  }

  // wrapper- c     .

  &__wrapper {
    margin-top: 10px;
    // margin-left: 17px;
    margin-bottom: 19px;
    display: flex;
    justify-content: start;
    align-items: center;
    // background-color: slateblue;
  }

  &__wrapper--levon {
    margin-top: 9px;
  }

  &__wrapper--fair {
    margin-top: 11px;
    margin-bottom: 20px;
  }

  &__wrapper--maxim {
    margin-bottom: 20px;
  }

  &__wrapper--hope {
    margin-bottom: 20px;
  }

  &__button {
    width: 13px;
    height: 12px;
    border: none;
    background: url("../img/picture/icon-heart-svg-copy-3.png") no-repeat 0 0, #f2f2f2;
  }

  &__like {
    margin-left: 19px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 12px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    text-transform: uppercase;
    color: #d22856;
  }

  &__amount {
    margin-left: 5px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 12px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2;
    letter-spacing: normal;
    text-transform: uppercase;
    color: #d22856;
  }

  &__publish-title-container {
    margin-top: 40px;
    margin-bottom: 42px;
  }

  &__publish-title {
    width: 255px;
    margin: 0 auto;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.16;
    letter-spacing: normal;
    text-align: center;
    /* background-color: blueviolet; */
  }
}

