.advantages {
  // max-width: 320px;
  // height: 1002px;
  margin: 0 auto;
  background-color: #ffffff;
  border-top: 1px solid rgba(255, 255, 255, 0);
  border-bottom: 1px solid rgba(255, 255, 255, 0);
  // border-bottom: 1px solid #f2f2f2;
  box-sizing: border-box;
  background-image: linear-gradient(-6deg, #f2f2f2 76%, #ffffff 76%);

  &__slogan {
    max-width: 220px;
    margin: 0 auto;
    margin-top: 41px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 300;
    font-style: normal;
    line-height: 1.2;
    text-align: center;
    // background-color: red;
    color: #283645;
  }

  &__slogan--two {
    max-width: 220px;
    margin: 0 auto;
    margin-top: 30px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 300;
    font-style: normal;
    line-height: 1.2;
    text-align: center;
    // background-color: red;
    color: #283645;
  }

  &__list {
    // max-width: 320px;
    border-top: 1px solid rgba(255, 255, 255, 0);
    border-bottom: 1px solid rgba(255, 255, 255, 0);
    margin: 0;
    margin-top: 86px;
    // margin-bottom: 322px;
    list-style: none;
    padding: 0;
    // background-color: green;
  }

  &__title--one,
  &__title--two,
  &__title--three {
    max-width: 252px;
    margin: 0 auto;
    margin-top: 40px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    font-style: normal;
    line-height: 1.71;
    text-align: center;
    color: #000000;
    // background-color: salmon;
  }

  &__title--one {
    margin-top: 25px;
  }

  &__title--two {
    margin-top: 30px;
  }

  &__title--three {
    margin-top: 30px;
  }

  &__description {
    max-width: 270px;
    margin: 0 auto;
    margin-top: 10px;
    margin-bottom: 0;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    line-height: 1.71;
    text-align: center;
    color: #283645;
    // background-color: seagreen;
  }

  &__photo-container {
    // max-width: 320px;
    width: 157px;
    // height: 320px;
    height: 277px;
    margin: 0 auto;
    margin-top: 43px;
    overflow-y: hidden;
    // border: 1px solid red;
    border-radius: 23px 23px 0 0;
    // background: url("../img/iphone-screen-mobile@1x.png") no-repeat center 42px, #f2f2f2;
  }

  &__photo-mobile-tablet {
    width: 157px;
    height: 320px;
    // object-fit: contain;
  }

  &__break {
    display: none;
  }

  &__break--tablet {
    display: none;
  }

  &__photo-container--one,
  &__photo-container--two,
  &__photo-container--three {
    display: none;
  }
}

@media (min-width: 660px) {
  .advantages {
    min-width: 660px;
    margin: 0 auto;
    background-color: #ffffff;
    border-top: 1px solid rgba(255, 255, 255, 0);
    box-sizing: border-box;
    background-image: linear-gradient(-4deg, #f2f2f2 62.5%, #ffffff 62.5%);

    &__content-wrapper {
      width: 660px;
      margin: 0 auto;
    }

    &__slogan {
      display: block;
      max-width: 620px;
      margin: 0 auto;
      margin-top: 53px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 40px;
      font-weight: 300;
      font-style: normal;
      line-height: 1;
      text-align: center;
      // background-color: red;
      color: #283645;
    }

    &__slogan--two {
      display: block;
      max-width: 350px;
      margin: 0 auto;
      margin-top: 5px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 40px;
      font-weight: 300;
      font-style: normal;
      line-height: 1.2;
      text-align: center;
      // background-color: red;
      color: #283645;
    }

    &__wrapper-list {
      display: flex;
      justify-content: start;
      align-items: flex-start;
      margin-top: 50px;
      // background-color: red;
    }

    &__list {
      // max-width: 320px;
      border-top: 1px solid rgba(255, 255, 255, 0);
      border-bottom: 1px solid rgba(255, 255, 255, 0);
      margin: 0;
      margin-top: 28px;
      margin-left: 42px;
      // margin-bottom: 322px;
      list-style: none;
      padding: 0;
      order: 2;
      // background-color: green;
      display: flex;
      flex-direction: column;
      justify-content: start;
      align-items: start;
    }

    &__photo-container {
      width: 268px;
      height: 481px;
      // border: 1px solid red;
      margin: 0;
      margin-left: 20px;
      overflow-y: hidden;
      border-radius: 40px 40px 0 0;
      // background: url("../img/iphone-screen-mobile@1x.png") no-repeat center 45px, #f2f2f2;
      // background: url("../img/iphone-screen-3-tablet@1x.png") no-repeat 0 0;
      order: 1;
    }

    &__photo-mobile-tablet {
      width: 268px;
      height: 547px;
      // object-fit: contain;
    }

    // &__title

    &__title--one {
      text-align: left;
      margin-left: 0;
      margin-top: 38px;
      // background-color: purple;
    }

    &__title--two {
      text-align: left;
      margin-left: 0;
      margin-top: 33px;
    }

    &__title--three {
      text-align: left;
      margin-left: 0;
      margin-top: 34px;
    }

    &__description {
      max-width: 316px;
      text-align: left;
      margin-left: 0;
      margin-top: 10px;
    }

    &__break--tablet {
      display: block;
    }
  }
}

@media (min-width: 1200px) {
  .advantages {
    min-width: 1200px;
    margin: 0 auto;
    background-color: #ffffff;
    border-top: 1px solid rgba(255, 255, 255, 0);
    // border-top: 1px solid red;
    border-bottom: 1px solid #f2f2f2;
    box-sizing: border-box;
    background-image: linear-gradient(-4.4deg, #f2f2f2 59.4%, #ffffff 59.4%);

    &__content-wrapper {
      width: 1200px;
      margin: 0 auto;
    }

    &__slogan {
      max-width: 750px;
      margin: 0 auto;
      margin-top: 66px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 50px;
      font-weight: 300;
      font-style: normal;
      line-height: 1.2;
      text-align: center;
      // background-color: red;
      color: #283645;
    }

    &__slogan--two {
      max-width: 450px;
      margin: 0 auto;
      // margin-top: 20px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 50px;
      font-weight: 300;
      font-style: normal;
      line-height: 1.2;
      text-align: center;
      // background-color: red;
      color: #283645;
    }

    &__wrapper-list {
      display: block;
      margin: 0 auto;
      margin-top: 50px;
      // background-color: red;
    }

    &__list {
      // width: 872px;
      width: 940px;
      // max-width: 320px;
      border-top: 1px solid rgba(255, 255, 255, 0);
      border-bottom: 1px solid rgba(255, 255, 255, 0);
      margin: 0 auto;
      margin-top: 75px;
      // margin-left: 44px;
      margin-bottom: 52px;
      list-style: none;
      padding: 0;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: flex-start;
      // background-color: green;
    }

    &__photo-container {
      display: none;
    }

    &__item {
      display: flex;
      flex-direction: column;
      justify-content: start;
      align-items: center;
      // background-color: tan;
    }

    &__photo-container--one {
      display: block;
      width: 300px;
      height: 302px;
      // background: url("../img/iphone-screen-1-desktop@1x.png") no-repeat center top;
    }

    &__photo-container--two {
      display: block;
      width: 300px;
      height: 302px;
      // background: url("../img/iphone-screen-2-desktop@1x.png") no-repeat 7px 0;
    }

    &__photo-container--three {
      display: block;
      width: 300px;
      height: 302px;
      // background: url("../img/iphone-screen-3-desktop@1x.png") no-repeat 7px 0;
    }

    // &__title

    &__title--one,
    &__title--two,
    &__title--three {
      max-width: 232px;
      margin: 0 auto;
      margin-top: 55px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 30px;
      font-weight: bold;
      font-style: normal;
      line-height: 1;
      text-align: center;
      color: #000000;
      // background-color: #ffffff;
    }

    &__description {
      max-width: 250px;
      margin: 0 auto;
      margin-top: 25px;
      margin-bottom: 0;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 18px;
      font-weight: normal;
      font-style: normal;
      line-height: 1.67;
      text-align: center;
      color: #283645;
      // background-color: #ffffff;
    }

    &__break--tablet {
      display: none;
    }

    &__break {
      display: block;
    }
  }
}
