.page-header {
  // position: absolute;
  // top: 0;
  // left: 0;
  // right: 0;
  min-width: 320px;
  margin: 0 auto;
  box-sizing: border-box;
  background-color: #283645;
  // background-color: rgba(0, 0, 0, 0.4);
  border-top: 1px solid rgba(255, 255, 255, 0);

  // &.not-transparent {
  //   position: static;
  //   min-width: 320px;
  //   margin: 0 auto;
  //   box-sizing: border-box;
  //   background-color: #283645;
  //   // background-color: rgba(0, 0, 0, 0.4);
  //   border-top: 1px solid rgba(255, 255, 255, 0);
  // }

  &__logo-wrapper {
    // max-width: 320px;
    margin: 0 auto;
    padding-top: 21px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 26px;
    display: flex;
    justify-content: space-between;
    align-items: start;
    box-shadow: 0 1px 0 0 #000000, 0 2px 0 0 rgba(255, 255, 255, 0.15);
    // box-shadow: 0 1px 0 0 #000000;
    // background-color: green;
  }

  // &__limit {
  //   width: 280px;
  //   margin: 0 auto;
  //   display: flex;
  //   justify-content: space-between;
  //   align-items: start;
  //   // background-color: green;
  // }

  &__logo {
    width: 75px;
    height: 22px;
    // margin-left: 20px;
    text-decoration: none;
    background: url("../img/picture/logo-pink-white-mobile.svg") no-repeat 0 0;
    // background-color: red;
  }

  &__image {
    width: 100%;
    height: 100%;

    &:hover {
      path {
        fill: #d22856;
      }
    }

    &:active {
      path {
        fill: #ffffff;
        opacity: 0.3;
      }
    }
  }

  // &__image {
  //   display: none;
  //   // background-color: green;
  // }

  // ===================================

  &__logo-pink {
    display: inline;
    fill: #ffffff;
  }

  &__logo-pink--tablet {
    display: none;
  }

  &__logo-glasses {
    display: none;
    fill: #ffffff;
  }

  &__logo-star {
    display: none;
    fill: #d22856;
  }

  &__button-toggle {
    display: none;
    width: 24px;
    height: 23px;
    // margin-right: 20px;
    background: url("../img/picture/icon-menu-cross.svg") no-repeat 0 0, #283645;
    border: none;
  }

  // &__button-toggle {
  //   width: 50px;
  //   height: 24px;
  //   // margin-right: 20px;
  //   background: url("../img/picture/icon-menu-burger.svg") no-repeat 0 0, #283645;
  //   border: none;
  // }

  // &__button-toggle--cross {
  //   width: 24px;
  //   height: 23px;
  //   // margin-right: 20px;
  //   background: url("../img/picture/icon-menu-cross.svg") no-repeat 0 0, #283645;
  //   border: none;
  // }
}

