    top: 0;
    left: 0;
    right: 0;
    z-index: 20;
    min-width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
    border-top: 1px solid rgba(255, 255, 255, 0);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    background-color: rgba(0, 0, 0, 0.4);

    // &.not-transparent {
