.form {
  min-width: 320px;
  // width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: start;
  // align-items: start;
  align-items: center;
  // background-color: aqua;

  &__name-container {
    width: 280px;
    // align-self: start;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    // padding-left: 22px;
    // background-color: burlywood;
  }

  &__surname-label,
  &__name-label,
  &__father-name-label {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    // margin-left: 22px;
    // margin-bottom: 20px;
    // background-color: darkgreen;
  }

  &__name-label {
    margin-top: 17px;
  }

  &__father-name-label {
    margin-top: 17px;
    margin-bottom: 0;
  }

  &__name {
    margin-left: 21px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    line-height: 2.14;
    text-align: left;
    text-transform: uppercase;
    color: #d22856;
  }

  &__name-field {
    width: 280px;
    height: 63px;
    border: solid 2px #e5e5e5;
    margin-top: 6px;
    // margin-bottom: 28px;
    padding-left: 20px;
    padding-top: 7px;
    padding-bottom: 8px;
    box-sizing: border-box;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: normal;
    font-style: normal;
    line-height: 1.67;
    text-align: left;
    color: #283645;
  }

  &__name-field:hover {
    border: solid 2px #283645;
  }

  &__name-field:focus {
    border: solid 2px #d22856;
  }

  // &__title-container {
  //   width: 320px;
  //   margin: 0 auto;
  //   border-top: 1px solid #f2f2f2;
  //   border-bottom: 1px solid #f2f2f2;
  //   background-color: #f2f2f2;
  // }

  &__title {
    width: 320px;
    height: 32px;
    margin: 0 auto;
    margin-top: 42px;
    // margin-bottom: 42px;
    padding-top: 38px;
    padding-bottom: 42px;
    background-color: #f2f2f2;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    line-height: 1.29;
    text-transform: uppercase;
    text-align: center;
    color: #d22856;
    // background-color: steelblue;
  }

  &__achievement-container {
    width: 320px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    padding-left: 20px;
    background-color: #f2f2f2;
    box-sizing: border-box;
    // background-color: tomato;
  }

  &__achievement-label {
    margin-bottom: 43px;
    display: flex;
    justify-content: start;
    align-items: center;
    // background-color: seagreen;
  }

  // &__achievement-label:hover .form__achievement-marker {
  //   border: solid 2px #283645;
  // }

  &__achievement-marker {
    width: 39px;
    height: 39px;
    border: solid 2px #e5e5e5;
    box-sizing: border-box;
    background-color: #ffffff;
  }

  &__achievement:checked + .form__achievement-marker {
    background: url("../img/picture/icon-yes-red.png") no-repeat center center, #ffffff;
    border: solid 2px #e5e5e5;
  }

  &__achievement-label:hover .form__achievement-marker {
    border: solid 2px #283645;
  }

  &__achievement-label:active .form__achievement-marker {
    border: solid 2px #d22856;
  }

  &__achievement-description {
    margin-left: 31px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    // letter-spacing: normal;
    text-align: left;
    color: #283645;
  }

  &__contact-information {
    display: none;
  }

  &__phone-email-wrapper {
    min-width: 320px;
    margin: 0 auto;
    // background-color: darkseagreen;
  }

  &__phone-email {
    align-self: start;
    margin-top: 32px;
    display: flex;
    flex-direction: column;
    align-items: start;
    padding-left: 20px;
    // background-color: steelblue;
  }

  &__information {
    display: flex;
    flex-direction: column;
    align-items: start;
    // background-color: tan;
  }

  // &__information:last-child {
  //   // margin-bottom: 35px;
  // }

  &__field-title {
    margin-left: 22px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 2.14;
    // letter-spacing: normal;
    text-align: left;
    text-transform: uppercase;
    color: #d22856;
  }

  &__contacts-field {
    width: 280px;
    height: 63px;
    margin-top: 7px;
    margin-bottom: 16px;
    border: solid 2px #e5e5e5;
    box-sizing: border-box;
    padding-left: 20px;
    padding-top: 6px;
    padding-bottom: 10px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    // letter-spacing: normal;
    text-align: left;
    color: #283645;
  }

  &__contacts-field:hover {
    border: solid 2px #283645;
  }

  &__contacts-field:focus {
    border: solid 2px #d22856;
  }

  &__input-image {
    display: none;
  }

  // &__big-radio-wrapper {

  //   margin: 0;
  //   background-color: darkviolet;
  // }

  &__radio-title {
    min-width: 320px;
    margin: 0 auto;
    margin-top: 25px;
    // margin-bottom: 35px;
    padding-top: 38px;
    padding-bottom: 34px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.29;
    // letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
    color: #d22856;
    background-color: #f2f2f2;
  }

  &__radio-container {
    width: 320px;
    margin-bottom: 32px;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: start;
    padding-left: 21px;
    box-sizing: border-box;
    background-color: #f2f2f2;
  }

  &__radio-label {
    margin-bottom: 37px;
    display: flex;
    justify-content: start;
    align-items: center;
    // background-color: #2aab6d;
  }

  &__radio-label:last-child {
    margin-bottom: 43px;
  }

  &__radio-marker {
    width: 38px;
    height: 38px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: solid 2px #e5e5e5;
    box-sizing: border-box;
    background-color: #ffffff;
  }

  &__radio-description {
    margin-left: 30px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    // letter-spacing: normal;
    text-align: left;
    color: #283645;
  }

  &__radio-item:checked ~ .form__radio-marker circle {
    fill: #d22856;
  }

  &__radio-item:checked ~ .form__radio-marker {
    // border: solid 2px #d22856;
    border: solid 2px #e5e5e5;
  }

  &__radio-label:hover .form__radio-marker {
    border: solid 2px #283645;
  }

  &__radio-label:active .form__radio-marker {
    border: solid 2px #d22856;
  }

  &__textarea-title {
    margin: 0 auto;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 2.14;
    // letter-spacing: normal;
    text-transform: uppercase;
    text-align: center;
    color: #d22856;
  }

  &__textarea-wrapper {
    // max-width: 320px;
    // height: 300px;
    margin: 0 auto;
    margin-top: 12px;
    padding-left: 20px;
    padding-right: 20px;
    // border-top: 1px solid ;
    box-sizing: border-box;
    // background-color: #2aab6d;
  }

  &__emotions {
    display: block;
    max-width: 280px;
    height: 200px;
    resize: none;
    margin: 0 auto;
    // margin-top: 15px;
    border: solid 2px #e5e5e5;
    box-sizing: border-box;
    padding-top: 14px;
    padding-left: 19px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    // letter-spacing: normal;
    text-align: left;
    color: #283645;
  }

  &__submit-required-wrapper {
    margin-bottom: 16px;
  }

  &__submit-button {
    width: 280px;
    height: 63px;
    margin-top: 49px;
    border: solid 2px #2aab6d;
    border-radius: 2px;
    background-color: #2aab6d;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    font-style: normal;
    line-height: 1.67;
    text-transform: uppercase;
    text-align: center;
    color: #ffffff;
  }

  &__submit-button:hover {
    background-color: #1c9b5e;
  }

  &__submit-button:active {
    background-color: #1c9b5e;
    color: rgba(255, 255, 255, 0.3);
  }

  &__required-field {
    margin: 0 auto;
    margin-top: 36px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 2.14;
    // letter-spacing: normal;
    text-transform: uppercase;
    text-align: center;
    color: #283645;
  }

  &__star {
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 2.14;
    color: #d22856;
  }

  &__two-words {
    display: none;
  }
}

