.feedback {
  max-width: 320px;
  margin: 0 auto;
  // background-color: darkgreen;

  &__slider-container {
    max-width: 320px;
    position: relative;
    margin: 0 auto;
  }

  &__text-wrapper {
    max-width: 280px;
    margin: 0 auto;
    // background-color: royalblue;
  }

  &__name {
    margin: 0 auto;
    margin-top: 40px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    // letter-spacing: normal;
    text-align: center;
    color: #000000;
  }

  &__text {
    display: none;
  }

  &__comment {
    width: 280px;
    margin: 0 auto;
    margin-top: 13px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.17;
    letter-spacing: normal;
    text-align: center;
    color: #283645;
  }

  // &__slider--two,
  // &__slider--three {
  //   display: none;
  // }

  &__button-wrapper {
    width: 62px;
    margin: 0 auto;
    margin-top: 34px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    // background-color: aquamarine;
  }

  &__button {
    width: 6px;
    height: 6px;
    // border: none;
    border: 3px solid #ffffff;
    border-radius: 50%;
    opacity: 0.3;
    background-color: #283645;
  }

  &__slider--one {
    display: block;
  }

  &__slider--two {
    display: none;
  }

  &__slider--three {
    display: none;
  }

  &__button:hover {
    opacity: 0.6;
  }

  &__button.active {
    border: 3px solid #283645;
    background-color: #ffffff;
    opacity: 1;
  }

  &__prev {
    display: none;
  }

  &__next {
    display: none;
  }
}

@media (min-width: 660px) {
  .feedback {
    min-width: 660px;
    margin: 0 auto;
    // border-top: 1px solid red;

    &__slider-container {
      max-width: 660px;
      position: relative;
      margin: 0 auto;
      margin-top: 94px;
      // background-color: aqua;
    }

    &__container-inner {
      display: flex;
      flex-direction: column;
      justify-content: start;
      align-items: center;
      // background-color: burlywood;
    }

    &__text-wrapper {
      width: 400px;
      order: 2;
      // margin: 0 auto;
      // background-color: royalblue;
    }

    &__name {
      display: block;
      margin: 0 auto;
      margin-top: 30px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 20px;
      font-weight: bold;
      // font-stretch: normal;
      font-style: normal;
      line-height: 2;
      // letter-spacing: normal;
      text-align: center;
      color: #000000;
    }

    &__text {
      display: block;
      margin: 0 auto;
      text-align: center;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 14px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 2;
      letter-spacing: normal;
      color: #283645;
    }

    &__comment {
      width: 630px;
      margin: 0 auto;
      // margin-top: 106px;
      margin-top: 0;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 18px;
      font-weight: normal;
      // font-stretch: normal;
      font-style: normal;
      line-height: 1.67;
      // letter-spacing: normal;
      text-align: center;
      color: #283645;
      order: 1;
      // background-color: seagreen;
    }

    // &__text-wrapper {
    //   // width: 190px;
    //   // order: 2;
    //   // background-color: crimson;
    // }
  }
}

@media (min-width: 1200px) {
  .feedback {
    min-width: 1200px;
    margin: 0 auto;

    &__slider-container {
      max-width: 1200px;
      position: relative;
      margin: 0 auto;
      padding-top: 185px;
      background: url("../img/picture/quotes-svg.png") no-repeat 562px 72px, #ffffff;
    }

    &__button-wrapper {
      display: none;
    }

    &__prev {
      display: block;
      width: 22px;
      height: 41px;
      position: absolute;
      top: 213px;
      left: 130px;
      padding: 0;
      border: none;
      background-color: #ffffff;
      opacity: 0.3;
      // background: url("../img/left-arrow-svg.png") no-repeat 0 0, #ffffff;
    }

    &__prev:hover {
      opacity: 0.6;
    }

    &__prev:active {
      opacity: 0.1;
    }

    &__next {
      display: block;
      width: 22px;
      height: 41px;
      position: absolute;
      top: 213px;
      right: 130px;
      padding: 0;
      border: none;
      background-color: #ffffff;
      opacity: 0.3;
      // border: 1px solid red;
      // background: url("../img/right-arrow-svg.png") no-repeat 0 0, #ffffff;
    }

    &__next:hover {
      opacity: 0.6;
    }

    &__next:active {
      opacity: 0.1;
    }

    &__name {
      margin: 0 auto;
      margin-top: 33px;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 24px;
      font-weight: bold;
      // font-stretch: normal;
      font-style: normal;
      line-height: 1.5;
      // letter-spacing: normal;
      text-align: center;
      color: #000000;
    }

    &__text {
      display: block;
      margin: 0 auto;
      text-align: center;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 18px;
      font-weight: normal;
      font-stretch: normal;
      font-style: normal;
      line-height: 1.67;
      letter-spacing: normal;
      color: #283645;
    }

    &__comment {
      width: 700px;
      margin: 0 auto;
      // margin-top: 106px;
      margin-top: 0;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 18px;
      font-weight: normal;
      // font-stretch: normal;
      font-style: normal;
      line-height: 1.67;
      // letter-spacing: normal;
      text-align: center;
      color: #283645;
      order: 1;
    }

    &__text-wrapper {
      order: 2;
    }
  }
}
