.download-app {
  // max-width: 320px;
  // position: relative;
  margin: 0 auto;
  border-top: 1px solid rgba(255, 255, 255, 0);
  border-bottom: 1px solid #283645;
  box-sizing: border-box;
  // background: url("../img/picture/iphone-hand-mobile@1x.png") no-repeat calc(50% - 30px) 107px, url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center -69px, #283645;
  background: url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center -67px, #283645;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background: url("../img/picture/bg-back-mobile@2x.jpg") no-repeat center -67px, #283645;
  }

  // &.open {
  //   margin: 0 auto;
  //   background: url("../img/picture/iphone-hand-mobile@1x.png") no-repeat calc(50% - 30px) 107px, url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center -69px, #283645;
  //   // background: url("../img/picture/iphone-hand-mobile@1x.png") no-repeat calc(50% - 30px) 181px, url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center 5px, #283645;
  //   border-top: 1px solid rgba(255, 255, 255, 0);
  //   border-bottom: 1px solid #283645;
  //   box-sizing: border-box;
  //   padding-top: 0;
  // }

  &__align-container {
    position: relative;
  }

  &__wrapper {
    position: absolute;
    top: 0;
    left: calc(50% - 160px);
    // right: 0;
    width: 320px;
    // margin: 0 auto;
  }

  &__slogan {
    max-width: 240px;
    margin: 0 auto;
    margin-top: 22px;
    // margin-top: 24px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 300;
    font-style: normal;
    line-height: 1.2;
    text-align: center;
    color: #ffffff;
  }

  &__picture {
    // width: 258px;
    // height: 287px;
    margin: 0;
    padding: 0;
    // margin-top: 20px;
  }

  &__photo {
    width: 258px;
    height: 287px;
    // margin-top: 30px;
    margin-top: 108px;
    margin-bottom: 266px;
    margin-left: calc(50% - 160px);
  }

  &__link {
    display: block;
    width: 281px;
    height: 55px;
    // margin: 20px auto 32px;
    margin: 0 auto;
    margin-top: 345px;
    // margin-top: 25px;
    // margin-bottom: 31px;
    border: 3px solid #ffffff;
    border-radius: 27px;
    box-sizing: border-box;
    text-align: center;
    text-decoration: none;
    color: #ffffff;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    font-style: normal;
    line-height: 1;
    padding-top: 14px;
  }

  &__link:hover {
    background-color: #ffffff;
    color: #d22856;
  }

  &__link:active {
    background-color: #ffffff;
    color: rgba(210, 40, 86, 0.3);
  }

  &__list {
    width: 206px;
    margin: 0 auto;
    margin-top: 31px;
    padding: 0;
    list-style: none;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    // background-color: burlywood;
  }

  &__image--android {
    margin-top: 8px;
  }

  &__image--windows {
    margin-top: 11px;
  }

  &__notice {
    max-width: 235px;
    margin: 18px auto 30px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: 300;
    font-style: normal;
    line-height: 1.71;
    letter-spacing: normal;
    text-align: center;
    color: #ffffff;
  }
}

