    width: 280px;
    height: 55px;
    margin-top: 38px;
    margin-left: 18px;
    margin-bottom: 12px;
    border-radius: 27px;
    border: solid 3px #d22856;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    font-style: normal;
    line-height: 1;
    text-align: center;
    color: #d22856;
    padding-top: 14px;
    padding-bottom: 17px;
  }

  &__submit-button:hover {
    background-color: #d22856;
    color: #ffffff;
  }

  &__submit-button:active {
    color: rgba(255, 255, 255, 0.3);
  }

  &__reset-button {
