    height: 55px;
    margin-left: 18px;
    margin-bottom: 40px;
    border-radius: 27px;
    opacity: 0.1;
    border: 3px solid #283645;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    font-style: normal;
    line-height: 1;
    text-align: center;
    color: #283645;
    padding-top: 14px;
    padding-bottom: 17px;
  }

  &__reset-button:hover {
    opacity: 0.3;
  }

  &__reset-button:active {
    opacity: 0.3;
    background-color: #283645;
    color: #ffffff;
  }
