.prices {
  // max-width: 320px;
  min-width: 320px;
  margin: 0 auto;

  &__tablet {
    display: none;
  }

  &__gradient {
    // max-width: 320px;
    height: 120px;
    margin: 0 auto;
    background: linear-gradient(-5deg, #f2f2f2 55%, #ffffff 56%);
  }

  &__slider-container {
    margin: 0 auto;
    padding-top: 11px;
    border-top: 1px solid #f2f2f2;
    border-bottom: 1px solid #f2f2f2;
    background-color: #f2f2f2;
  }

  &__slider--one,
  &__slider--two,
  &__slider--three {
    // margin: 0 auto;
    background-color: #f2f2f2;
    // margin-top: 50px;
    display: flex;
    justify-content: start;
    align-items: flex-end;
  }

  // ===========================      ==========================================

  &__first-col {
    display: none;
  }

  &__table--one,
  &__table--two,
  &__table--three {
    margin: 0 auto;
    width: 320px;
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: flex-start;
    flex-wrap: wrap;
    box-sizing: border-box;
    // border-top: 2px solid red;
    // border-bottom: 2px solid red;
  }

  &__head--first,
  &__head--second,
  &__head--therd,
  &__data {
    // border: 2px solid #e5e5e5;
    box-sizing: border-box;
  }

  &__head--first,
  &__head--therd {
    width: 20px;
    height: 97px;
    // border-bottom: 2px solid #e5e5e5;
    background-color: #283645;
  }

  // ============================       ==================================

  &__head--second {
    // width: 280px;
    box-sizing: border-box;
    background-color: #283645;
  }

  &__data--hidden {
    // display: none;
    background-color: #ffffff;
    // background-color: cadetblue;
    width: 20px;
    border-bottom: 2px solid #e5e5e5;
    box-sizing: border-box;
    /* */
    height: 61px;
    /* */
  }

  // &__data {
  //   height: 50px;
  //   background-color: #ffffff;
  //   background-color: cornflowerblue;
  // }


  &__marker-container {
    width: 280px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background-color: #ffffff;
    // background-color: lightpink;
    border-bottom: 2px solid #e5e5e5;
    border-left: 2px solid #e5e5e5;
    border-right: 2px solid #e5e5e5;
    box-sizing: border-box;
  }

  &__marker-container--hit {
    width: 280px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background-color: #ffffff;
    // background-color: lightpink;
    border-bottom: 2px solid #e5e5e5;
    border-left: 2px solid #e5e5e5;
    border-right: 2px solid #e5e5e5;
    box-sizing: border-box;
  }

  &__picture-container--hit {
    position: relative;
    background-color: #283645;
    width: 280px;
    height: 97px;
    // background: url("../img/bg-hit.svg") no-repeat 0 0, #283645;
    box-sizing: border-box;
    // border-bottom: 2px solid #e5e5e5;
    border-left: 2px solid #e5e5e5;
    border-right: 2px solid #e5e5e5;
  }

  &__picture-container {
    width: 280px;
    height: 97px;
    background-color: #283645;
    box-sizing: border-box;
    // border-bottom: 2px solid #e5e5e5;
    border-left: 2px solid #e5e5e5;
    border-right: 2px solid #e5e5e5;
  }

  &__new {
    position: absolute;
    top: 0;
    left: -2px;
    z-index: 5;
  }

  &__word-hit {
    position: absolute;
    top: 12px;
    left: 9px;
    z-index: 10;
    transform: rotate(-45deg);
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.1;
    letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__sort {
    margin: 0 auto;
    // padding-top: 12px;
    padding-top: 15px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    // letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
    // background-color: red;
  }

  &__price {
    display: block;
    margin-top: 2px;
    // margin-bottom: 15px;
    margin-bottom: 13px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 300;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.25;
    // letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
    color: #ffffff;
  }

  &__marker {
    // display: block;
    position: relative;
    width: 68px;
    height: 59px;
    // background-color: yellow;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__marker::before {
    content: " ";
    position: absolute;
    left: -188px;
    top: 16px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.14;
    letter-spacing: normal;
    text-align: left;
    text-transform: uppercase;
    color: #283645;
  }

  &__marker--smile {
    position: relative;
    width: 68px;
    height: 59px;
    display: flex;
    justify-content: center;
    align-items: center;
    // background-color: yellow;
  }

  &__marker--smile::before {
    content: "";
    position: absolute;
    left: -188px;
    top: 16px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.14;
    letter-spacing: normal;
    text-align: left;
    text-transform: uppercase;
    color: #283645;
  }

  &__marker--comment {
    position: relative;
    width: 68px;
    height: 59px;
    display: flex;
    justify-content: center;
    align-items: center;
    // background-color: yellow;
  }

  &__marker--comment::before {
    content: "";
    position: absolute;
    left: -188px;
    top: 16px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 2.14;
    letter-spacing: normal;
    text-align: left;
    text-transform: uppercase;
    color: #283645;
  }

  &__no path {
    fill: #f25b26;
  }

  &__slider--one {
    display: none;
  }

  &__slider--two {
    display: none;
  }

  &__slider--three {
    display: none;
  }

  &__button-wrapper {
    width: 62px;
    margin: 0 auto;
    margin-top: 42px;
    margin-bottom: 45px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__button--one,
  &__button--two,
  &__button--three {
    width: 6px;
    height: 6px;
    border: 3px solid #f2f2f2;
    border-radius: 50%;
    background-color: rgba(40, 54, 69, 0.3);
  }

  &__switch--one:checked ~ .prices__slider-container .prices__slider--one {
    display: block;
  }

  &__switch--two:checked ~ .prices__slider-container .prices__slider--two {
    display: block;
  }

  &__switch--three:checked ~ .prices__slider-container .prices__slider--three {
    display: block;
  }

  // =================================================

  &__switch--one:checked ~ .prices__slider-container .prices__button--one {
    border: 3px solid #283645;
    background-color: #ffffff;
  }

  &__switch--two:checked ~ .prices__slider-container .prices__button--two {
    border: 3px solid #283645;
    background-color: #ffffff;
  }

  &__switch--three:checked ~ .prices__slider-container .prices__button--three {
    border: 3px solid #283645;
    background-color: #ffffff;
  }
}

