.site-list {
  // max-width: 320px;
  margin: 0 auto;
  padding: 0;
  list-style: none;
  box-shadow: 0 1px 0 0 #000000;
  border-bottom: 1px solid #000000;
  // min-width: 320px;
  // display: flex;
  // flex-direction: column;
  // justify-content: flex-start;
  // align-items: center;

  &__item {
    // max-width: 320px;
    // height: 63px;
    text-align: center;
    border: none;
    box-shadow: 0 1px 0 0 #000000, 0 2px 0 0 rgba(255, 255, 255, 0.15);
    // border-bottom: 1px solid #000000;
    // box-shadow: 0 1px 0 0 #000000, inset 0 2px 0 0 rgba(255, 255, 255, 0.15);
  }

  &__item:last-child {
    text-align: center;
    // border-bottom: 1px solid #000000;
    box-shadow: none;
  }

  &__link {
    display: block;
    // min-width: 320px;
    // height: 63px;
    text-decoration: none;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.78;
    letter-spacing: normal;
    padding-top: 16px;
    padding-bottom: 16px;
    color: #ffffff;
    text-align: center;
    text-transform: uppercase;
    box-sizing: border-box;
    // border-bottom: 1px solid #000000;
    // box-shadow: 0 1px 0 0 #000000, inset 0 2px 0 0 rgba(255, 255, 255, 0.15);
  }

  &__link:hover {
    color: #d22856;
  }

  &__link:active {
    color: #d22856;
    opacity: 0.3;
  }
}

@media (min-width: 660px) {
  .site-list {
    // border-bottom: 1px solid #000000;
    box-shadow: 0 1px 0 0 #000000;

    &__item {
      text-align: center;
      box-shadow: 0 1px 0 0 #000000, 0 2px 0 0 rgba(255, 255, 255, 0.15);
      // border-bottom: 1px solid #000000;
      // box-shadow: 0 1px 0 0 #000000, inset 0 2px 0 0 rgba(255, 255, 255, 0.15);
    }
  }
}

@media (min-width: 1200px) {
  .site-list {
    min-width: 621px;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: none;
    box-shadow: none;
    // background-color: violet;

    &__item {
      max-width: 300px;
      height: 38px;
      box-sizing: border-box;
      border: none;
      box-shadow: none;
      // background-color: red;
    }

    &__item:last-child {
      text-align: center;
      border: none;
    }

    &__item-active {
      border-bottom: 2px solid #ffffff;
    }

    // &__item-active:hover {
    //   border-bottom: 2px solid #d22856;
    // }

    &__link {
      display: block;
      // max-width: 300px;
      // height: 50px;
      text-decoration: none;
      font-family: "Open Sans", "Arial", sans-serif;
      font-size: 18px;
      font-weight: normal;
      padding: 0;
      box-sizing: border-box;
      color: #ffffff;
      text-transform: none;
      border: none;
    }

    &__link:active {
      color: #ffffff;
      opacity: 0.3;
    }
  }
}
