.main-form {
  min-width: 320px;
  // position: relative;
  margin: 0 auto;
  background-color: #ffffff;

  &__header {
    min-width: 320px;
    margin: 0 auto;
    border-top: 1px solid rgba(255, 255, 255, 0);
    border-bottom: 1px solid rgba(255, 255, 255, 0);
    background: url("../img/picture/bg-back-mobile@1x.jpg") no-repeat center -68px, #283645;
  }

  &__title {
    max-width: 240px;
    margin: 29px auto 52px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 24px;
    font-weight: 300;
    font-style: normal;
    line-height: 1.22;
    text-align: center;
    color: #ffffff;
  }

  &__offer-wrapper {
    width: 280px;
    margin: 0 auto;
    margin-top: 45px;
    margin-bottom: 34px;
    text-align: center;
  }

  &__offer {
    width: 280px;
    margin: 0 auto;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    line-height: 2.16;
    text-align: center;
    color: #283645;
    // background-color: aquamarine;
  }

  &__break-desctop {
    display: none;
  }

  &__offer--two {
    display: none;
  }

  &__popup-error-wrapper {
    display: none;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 300px;
    z-index: 10;
  }

  &__popup--error {
    width: 320px;
    // position: fixed;
    // left: 0;
    // right: 0;
    // bottom: 300px;
    // z-index: 10;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    box-shadow: 0 7px 15px 0 rgba(0, 1, 1, 0.15);
    background-color: #ffffff;
  }

  &__error-title {
    margin: 0 auto;
    margin-top: 37px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.71;
    // letter-spacing: normal;
    text-align: center;
    color: #000000;
    // background-color: springgreen;
  }

  &__break {
    display: none;
  }

  &__error-advice {
    width: 300px;
    margin-top: 24px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.71;
    // letter-spacing: normal;
    text-align: center;
    color: #283645;
    // background-color: slateblue;
  }

  &__error-button {
    width: 280px;
    height: 63px;
    margin: 0 auto;
    margin-top: 25px;
    margin-bottom: 50px;
    box-sizing: border-box;
    border: solid 2px #2aab6d;
    border-radius: 2px;
    background-color: #2aab6d;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    // letter-spacing: normal;
    text-align: center;
    color: #ffffff;
  }

  &__popup-success-wrapper {
    display: none;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 300px;
    z-index: 10;
  }

  &__popup--success {
    width: 320px;
    // position: fixed;
    // left: 0;
    // right: 0;
    // bottom: 300px;
    // z-index: 10;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    box-shadow: 0 7px 15px 0 rgba(0, 1, 1, 0.15);
    background-image: linear-gradient(to bottom, #ffffff 57%, #f2f2f2 57%)
    // background-color: #ffffff;
  }

  &__success-title {
    margin: 0 auto;
    margin-top: 36px;
    // margin-bottom: 20px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.71;
    // letter-spacing: normal;
    text-align: center;
    color: #000000;
    // background-color: springgreen;
  }

  &__success-confirmation {
    // width: 270px;
    width: 280px;
    margin: 0 auto;
    margin-top: 23px;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.73;
    // letter-spacing: normal;
    text-align: center;
    color: #283645;
    // background-color: slateblue;
  }

  &__success-button {
    width: 280px;
    height: 63px;
    margin-top: 79px;
    margin-bottom: 52px;
    border: solid 2px #2aab6d;
    border-radius: 2px;
    background-color: #2aab6d;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 18px;
    font-weight: bold;
    // font-stretch: normal;
    font-style: normal;
    line-height: 1.67;
    // letter-spacing: normal;
    text-align: center;
    text-transform: uppercase;
    padding-top: 14px;
    padding-bottom: 31px;
    color: #ffffff;
  }
}

