@media (max-width: 659px) {
  .footer-container {
    display: none;
  }
}

@media (min-width: 660px) {
  .footer-container {
    display: block;
    width: 660px;
    margin: 0 auto;
    background-color: #ffffff;
  }
}

@media (min-width: 1200px) {
  .footer-container {
    display: block;
    width: 1200px;
    margin: 0 auto;
    background-color: #ffffff;
  }
}
