.page-footer {
  // display: none;
  min-width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  // background-color: aqua;

  &__logo {
    width: 147px;
    height: 40px;
    // margin-top: 56px;
    margin-top: 40px;
    // display: block;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    // border: 1px solid red;
    // background: url("../img/picture/logotype-blue-mobile-svg.png") no-repeat 0 0;
    // background: url("../img/picture/logo-pink-blue-mobile.svg") no-repeat 0 0;
  }

  // &__image {
  //   height: 100%;
  // }

  // &__logo:hover .page-footer__mobile-tablet path {
  //   fill: #d22856;
  // }

  // &__logo:active .page-footer__mobile-tablet path {
  //   opacity: 0.3;
  // }

  // &__logo:active .page-footer__mobile-tablet .page-footer__pink {
  //   fill: #283645;
  //   opacity: 0.3;
  // }

  &__image {
    height: 100%;

    &:hover {
      path {
        fill: #d22856;
      }
    }

    &:active {
      path {
        fill: #283645;
        opacity: 0.3;
      }
    }
  }

  &__logo-pink {
    // display: inline;
    display: none;
    fill: #ffffff;
  }

  // &__logo-pink--tablet {
  //   display: none;
  // }

  &__logo-glasses {
    // display: none;
    display: inline;
    // fill: #ffffff;
    fill: #d22856;
  }

  &__logo-star {
    display: none;
    fill: #d22856;
  }

  &__logo-pink--tablet {
    display: inline;
    fill: #283645;
  }

  &__logo-pink--tablet:hover {
    // display: inline;
    fill: #d22856;
  }

  &__logo-glasses--tablet {
    display: inline;
    fill: #d22856;
  }

  &__list {
    // max-width: 176px;
    max-width: 182px;
    list-style: none;
    margin: 0;
    margin-top: 40px;
    padding: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    // background-color: royalblue;
    // border: 2px solid red;
  }

  &__item {
    margin-right: 11px;
  }

  &__item:nth-child(3) {
    margin-right: 0;
  }

  &__link {
    width: 52px;
    height: 52px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    border: 3px solid #f2f2f2;
    box-sizing: border-box;
  }

  &__link:hover {
    border: 3px solid #283645;
    box-sizing: border-box;
  }

  &__link:active {
    opacity: 0.1;
    border: 3px solid #283645;
    box-sizing: border-box;
  }

  &__academy-link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 128px;
    height: 34px;
    margin-top: 38px;
    margin-bottom: 40px;
    box-sizing: border-box;
    border: none;
    // background-color: lightblue;
    // background: url("../img/picture/logo-htmlacademy.svg") no-repeat 101px 0;
    text-decoration: none;
  }

  &__academy-word {
    display: block;
    margin: 0;
    font-family: "Open Sans", "Arial", sans-serif;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    line-height: 1.29;
    text-align: left;
    // padding-top: 7px;
    padding: 0;
    padding-top: 7px;
    padding-bottom: 10px;
    color: #283645;
  }

  &__academy-svg path {
    fill: #d4345f;
  }

  &__academy-link:hover .page-footer__academy-svg path {
    fill: #283645;
  }

  &__academy-link:active .page-footer__academy-svg path {
    fill: #283645;
    opacity: 0.3;
  }
}

